.app-shell {
  min-height: 100vh;
}

.player-shell {
  min-height: 100vh;
  background: radial-gradient(circle at 20% 20%, #1b4965, #0b132b 55%),
    radial-gradient(circle at 80% 0%, #5bc0be, transparent 40%),
    #0b132b;
  color: #e9edf2;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 32px;
}

.player-card {
  width: min(520px, 100%);
  background: rgba(14, 25, 45, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 20px;
  padding: 32px;
  box-shadow: 0 24px 60px rgba(0, 0, 0, 0.4);
}

.player-card h1 {
  margin: 8px 0 12px;
  font-size: 34px;
}

.lead {
  color: #cdd7e3;
  font-size: 17px;
  margin: 0 0 18px;
}

.pill {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 6px 12px;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 999px;
  font-size: 13px;
  color: #dce4ee;
}

.actions {
  display: flex;
  gap: 12px;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 16px;
}

.cta {
  background: linear-gradient(135deg, #56cfe1, #5bc0be);
  color: #0b132b;
  padding: 12px 16px;
  border-radius: 12px;
  font-weight: 700;
  text-decoration: none;
  box-shadow: 0 10px 30px rgba(91, 192, 190, 0.35);
}

.ghost {
  color: #e9edf2;
  text-decoration: none;
  padding: 12px 14px;
  border: 1px solid rgba(255, 255, 255, 0.24);
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.06);
  font-weight: 600;
  text-align: center;
  display: inline-block;
}

.player-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 12px;
}

.game-shell {
  min-height: 100vh;
  background: #0b132b;
  color: #e9edf2;
  padding: 32px;
}

.game-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 24px;
}

.game-actions {
  display: flex;
  gap: 10px;
  align-items: center;
}

.game-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 16px;
}

.game-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 18px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.35);
}

.game-card h3 {
  margin: 6px 0 10px;
}

.game-card p {
  margin: 0 0 14px;
  color: #cdd7e3;
}

.auth-shell {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: radial-gradient(circle at 20% 20%, #1b4965, #0b132b 55%),
    #0b132b;
  color: #e9edf2;
  padding: 32px;
}

.auth-card {
  background: rgba(14, 25, 45, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 20px;
  padding: 32px;
  width: min(480px, 100%);
  box-shadow: 0 24px 60px rgba(0, 0, 0, 0.4);
}

.card-header h1 {
  margin: 0;
  font-size: 28px;
  color: #f7fafc;
}

.card-header p {
  margin: 8px 0 0;
  color: #b8c2cf;
}

.alert {
  background: rgba(255, 107, 107, 0.12);
  border: 1px solid rgba(255, 107, 107, 0.4);
  color: #ffc2c2;
  padding: 12px 14px;
  border-radius: 12px;
  margin: 16px 0;
  font-size: 14px;
}

.success {
  background: rgba(91, 192, 190, 0.15);
  border: 1px solid rgba(91, 192, 190, 0.4);
  color: #5bc0be;
  padding: 12px 14px;
  border-radius: 12px;
  margin: 16px 0;
  font-size: 14px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 20px;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.field span {
  font-size: 14px;
  color: #cdd7e3;
}

.field input {
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.12);
  color: #f7fafc;
  padding: 12px 14px;
  border-radius: 12px;
  font-size: 15px;
  transition: border-color 0.2s ease, background 0.2s ease;
}

.field input:focus {
  outline: none;
  border-color: #5bc0be;
  background: rgba(91, 192, 190, 0.08);
}

.primary,
.secondary {
  border: none;
  border-radius: 12px;
  padding: 12px 16px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.1s ease, box-shadow 0.2s ease, opacity 0.2s ease;
}

.primary {
  background: linear-gradient(135deg, #56cfe1, #5bc0be);
  color: #0b132b;
  box-shadow: 0 10px 30px rgba(91, 192, 190, 0.35);
}

.primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.secondary {
  background: rgba(255, 255, 255, 0.06);
  color: #e9edf2;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.secondary.full,
.ghost.full {
  width: 100%;
  display: inline-block;
}

.primary:not(:disabled):hover,
.secondary:hover,
.ghost:hover {
  transform: translateY(-1px);
}

.back-link {
  display: inline-block;
  margin-top: 12px;
  color: #9fb3c8;
  text-decoration: none;
  font-weight: 600;
}

.eyebrow {
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: #9fb3c8;
}

.user-email {
  margin: 4px 0 0;
  font-size: 16px;
  color: #f7fafc;
}

.admin-layout {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 260px 1fr;
  background: #0b132b;
  color: #e9edf2;
}

.sidebar {
  background: rgba(14, 25, 45, 0.95);
  border-right: 1px solid rgba(255, 255, 255, 0.08);
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.brand {
  display: flex;
  align-items: center;
  gap: 10px;
}

.dot {
  width: 12px;
  height: 12px;
  background: #5bc0be;
  border-radius: 50%;
  box-shadow: 0 0 10px #5bc0be;
}

.brand-title {
  margin: 0;
  font-weight: 700;
}

.brand-sub {
  margin: 0;
  color: #9fb3c8;
  font-size: 13px;
}

.user-chip {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px;
  background: rgba(255, 255, 255, 0.04);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.06);
}

.avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #56cfe1;
  color: #0b132b;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
}

.menu {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.menu-item {
  background: transparent;
  border: 1px solid transparent;
  color: #e9edf2;
  text-align: left;
  padding: 12px 14px;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  transition: background 0.2s ease, border-color 0.2s ease, transform 0.1s ease;
}

.menu-item:hover {
  background: rgba(255, 255, 255, 0.04);
  transform: translateY(-1px);
}

.menu-item.active {
  background: rgba(91, 192, 190, 0.15);
  border-color: rgba(91, 192, 190, 0.5);
  color: #56cfe1;
}

.sidebar-footer {
  margin-top: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.content {
  padding: 32px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 12px;
  margin-top: 12px;
}

.info-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 14px;
}

.firm-grid {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 12px;
}

.firm-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 14px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.firm-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.firm-head h3 {
  margin: 0;
}

.firm-games {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.game-pill {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.07);
  border-radius: 10px;
  padding: 10px 12px;
}

.game-pill-info {
  display: flex;
  align-items: center;
  gap: 8px;
}

.status {
  font-size: 13px;
  font-weight: 700;
  padding: 4px 10px;
  border-radius: 999px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.status.aktif {
  background: rgba(87, 204, 153, 0.16);
  color: #57cc99;
  border: 1px solid rgba(87, 204, 153, 0.35);
}

.status.beklemede {
  background: rgba(255, 193, 7, 0.16);
  color: #ffc107;
  border: 1px solid rgba(255, 193, 7, 0.35);
}

.status.pasif {
  background: rgba(239, 71, 111, 0.16);
  color: #ef476f;
  border: 1px solid rgba(239, 71, 111, 0.35);
}

.mini-btn {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.12);
  color: #e9edf2;
  border-radius: 8px;
  padding: 6px 10px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.1s ease, background 0.2s ease;
}

.mini-btn:hover {
  background: rgba(91, 192, 190, 0.18);
  color: #56cfe1;
  transform: translateY(-1px);
}

.firm-layout {
  display: grid;
  grid-template-columns: minmax(0, 1.2fr) minmax(0, 1fr);
  gap: 16px;
}

.firm-list,
.firm-manage {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.06);
  border-radius: 14px;
  padding: 14px;
}

.firm-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.firm-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 12px;
  margin: 12px 0;
}

.firm-manage-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.firm-games-manage {
  margin-top: 12px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.game-manage-row {
  display: flex;
  flex-direction: column;
  gap: 10px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.07);
  border-radius: 10px;
  padding: 10px 12px;
}

.game-manage-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.game-manage-row select {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  color: #e9edf2;
  border-radius: 10px;
  padding: 8px 10px;
  font-weight: 600;
}

.game-manage-actions {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 10px;
}

.game-manage-actions .field {
  margin: 0;
}

.asset-list,
.pair-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 6px;
}

.asset-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 6px;
}

.asset-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding: 8px 10px;
  background: rgba(255, 255, 255, 0.04);
  border-radius: 8px;
}

.asset-item a {
  color: #56cfe1;
  text-decoration: none;
  font-weight: 600;
  flex: 1;
}

.pair-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 12px;
  padding: 10px 12px;
  background: rgba(255, 255, 255, 0.04);
  border-radius: 8px;
  position: relative;
}

.pair-item .muted {
  flex: 1;
}

.pair-symbol {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.06);
  border-radius: 8px;
  flex-shrink: 0;
}

.pair-symbol img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 4px;
}

.delete-btn {
  background: rgba(255, 107, 107, 0.15);
  border: 1px solid rgba(255, 107, 107, 0.4);
  color: #ff6b6b;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 20px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  flex-shrink: 0;
  transition: background 0.2s ease, transform 0.1s ease;
}

.delete-btn:hover {
  background: rgba(255, 107, 107, 0.25);
  transform: scale(1.1);
}

.delete-btn:active {
  transform: scale(0.95);
}

.firm-manage-actions-row,
.game-save-row {
  display: flex;
  justify-content: flex-end;
  margin-top: 6px;
}

.games-manage {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.games-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.games-head select {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  color: #e9edf2;
  border-radius: 10px;
  padding: 8px 10px;
  font-weight: 600;
}

.game-manage-stack {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.game-card-manage {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.07);
  border-radius: 12px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.game-manage-actions-2 {
  grid-template-columns: 1fr 1fr auto;
}

@media (max-width: 960px) {
  .firm-layout {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 960px) {
  .player-shell {
    padding: 20px;
  }

  .admin-layout {
    grid-template-columns: 1fr;
  }

  .sidebar {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
  }

  .content {
    padding: 20px;
  }
}

@media (max-width: 640px) {
  .player-card {
    padding: 24px;
  }

  .game-shell {
    padding: 20px;
  }

  .sidebar {
    gap: 8px;
  }
}

.danger-hunter-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.danger-hunter-info {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 16px;
}

.danger-hunter-info p {
  margin: 4px 0;
}

.danger-hunter-image-wrapper {
  display: flex;
  justify-content: center;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 20px;
  overflow: auto;
}

.danger-hunter-image-container {
  position: relative;
  display: inline-block;
  cursor: crosshair;
  user-select: none;
}

.danger-hunter-image {
  max-width: 100%;
  max-height: 70vh;
  display: block;
  border-radius: 8px;
}

.danger-rectangle {
  position: absolute;
  border: 2px solid #ff6b6b;
  background: rgba(255, 107, 107, 0.2);
  box-sizing: border-box;
  z-index: 10;
}

.danger-rectangle:not(.danger-rectangle-preview) {
  pointer-events: all;
}

.danger-rectangle-preview {
  border: 2px dashed #5bc0be;
  background: rgba(91, 192, 190, 0.15);
}

.danger-rect-delete {
  position: absolute;
  top: -12px;
  right: -12px;
  background: rgba(255, 107, 107, 0.9);
  border: 1px solid #ff6b6b;
  color: white;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  pointer-events: all;
  transition: transform 0.1s ease;
}

.danger-rect-delete:hover {
  transform: scale(1.15);
  background: rgba(255, 107, 107, 1);
}

.danger-hunter-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 16px;
}

.danger-hunter-stats {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #cdd7e3;
  font-weight: 600;
}

@media (max-width: 640px) {
  .danger-hunter-controls {
    flex-direction: column;
  }

  .danger-hunter-image {
    max-height: 50vh;
  }
}

.coord-marker-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.coord-marker-modal {
  background: #0b132b;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  max-width: 90vw;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.coord-marker-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.coord-marker-header h3 {
  margin: 0;
  color: #f7fafc;
}

.coord-marker-content {
  padding: 20px;
  overflow: auto;
  flex: 1;
}

.coord-marker-image-wrapper {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 20px;
}

.coord-marker-image-container {
  position: relative;
  display: inline-block;
  cursor: crosshair;
  user-select: none;
}

.coord-marker-image {
  max-width: 100%;
  max-height: 60vh;
  display: block;
  border-radius: 8px;
  pointer-events: none;
}

.coord-marker-rectangle {
  position: absolute;
  border: 2px solid #ff6b6b;
  background: rgba(255, 107, 107, 0.2);
  box-sizing: border-box;
  cursor: pointer;
  z-index: 10;
}

.coord-marker-rectangle-preview {
  border: 2px dashed #5bc0be;
  background: rgba(91, 192, 190, 0.15);
  pointer-events: none;
}

.coord-rect-delete {
  position: absolute;
  top: -12px;
  right: -12px;
  background: rgba(255, 107, 107, 0.9);
  border: 1px solid #ff6b6b;
  color: white;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  pointer-events: all;
  transition: transform 0.1s ease;
}

.coord-rect-delete:hover {
  transform: scale(1.15);
  background: rgba(255, 107, 107, 1);
}

.coord-marker-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}

.coord-marker-actions {
  display: flex;
  gap: 10px;
}

.asset-preview {
  display: flex;
  gap: 12px;
  align-items: center;
  flex: 1;
}

.asset-preview img {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.asset-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.asset-info a {
  color: #56cfe1;
  text-decoration: none;
  font-weight: 600;
}

.asset-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.asset-image-fallback {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 107, 107, 0.1);
  border: 1px solid rgba(255, 107, 107, 0.3);
  border-radius: 8px;
  color: #ff6b6b;
  font-size: 11px;
